import React, { useEffect } from 'react';
import { NavBar, Footer, CaseStudyHero, CaseStudyContent, CaseStudyTakeaway } from '../components';
import { analytics } from '../utils/mixpanel';

const DeliverySystemCaseStudy = () => {
  useEffect(() => {
    // Track page view
    analytics.trackPageView('Delivery System Case Study', {
      timestamp: new Date().toISOString(),
      user_agent: navigator.userAgent,
      referrer: document.referrer || 'direct',
      screen_resolution: `${window.screen.width}x${window.screen.height}`,
      viewport_size: `${window.innerWidth}x${window.innerHeight}`,
    });
  }, []);

  return (
    <div className="min-h-screen bg-white">
      <NavBar />
      <CaseStudyHero />
      <CaseStudyContent />
      <CaseStudyTakeaway />
      <Footer />
    </div>
  );
};

export default DeliverySystemCaseStudy;