import React from 'react';

const HeroSection = () => {
  return (
    <section className="bg-white py-20 px-4 mt-16" id="home">
      <div className="max-w-2xl mx-auto text-center">
        {/* Illustration */}
        <div className="mb-10">
          <img
            src="/chaostoorder.png"
            alt="Chaos to Order"
            className="w-full max-w-sm mx-auto h-auto"
          />
        </div>

        {/* Headline */}
        <h1 className="text-4xl md:text-5xl font-bold text-apple-gray-800 mb-4 leading-tight">
          Fix Your Product. Scale with Confidence.
        </h1>

        {/* Subheadline */}
        <p className="text-lg md:text-xl text-apple-gray-600 mb-8 leading-relaxed">
          Hands-on product leadership for founders working with underperforming dev teams.
          Turn your SaaS into a scalable machine.
        </p>

        {/* Call to Action Button */}
        <div className="mb-4">
          <a
            href="https://form.typeform.com/to/SLoI0dkA"
            target="_blank"
            rel="noopener noreferrer"
            className="inline-block bg-apple-gray-800 text-white font-semibold py-4 px-8 rounded-lg hover:bg-apple-gray-700 transition-colors duration-200"
          >
            Book a Clarity Call
          </a>
        </div>

        {/* Supporting Line */}
        <p className="text-sm text-apple-gray-400">
          Free 30-min audit. No pitch. Just clarity.
        </p>
      </div>
    </section>
  );
};

export default HeroSection;