import React from 'react';

const CaseStudyContent = () => {
  return (
    <section className="bg-gray-50 py-16">
      <div className="max-w-6xl mx-auto px-6 lg:px-8">
        <div className="grid lg:grid-cols-2 gap-16 items-start">
          {/* Content */}
          <div className="space-y-12">
            {/* Challenge */}
            <div>
              <h2 className="text-2xl font-bold text-gray-900 mb-4">The Challenge</h2>
              <p className="text-gray-700 leading-relaxed text-lg">
                The client's delivery process was slow, rigid, and full of friction. Each feature request required a refinement step ("hashout") before work could begin. That hashout had to be estimated and approved, and the resulting dev tasks went through the same loop. Developers were often forced to stop mid-task when they hit their approved hour limit. Estimations became inflated, trust declined, and progress was constantly blocked.
              </p>
            </div>

            {/* Solution */}
            <div>
              <h2 className="text-2xl font-bold text-gray-900 mb-4">The Solution</h2>
              <p className="text-gray-700 leading-relaxed text-lg mb-6">
                I replaced the entire process with a point-based model that simplified approvals and restored flow:
              </p>
              <ul className="space-y-3 text-gray-700 text-lg">
                <li>• Introduced Fibonacci point estimates (1, 2, 3, 5, 8…)</li>
                <li>• Removed hashouts and hourly pre-approvals</li>
                <li>• Allowed developers to work without artificial stops</li>
                <li>• Tracked hours after delivery instead of before</li>
                <li>• Added a flat 20% buffer to account for product management</li>
                <li>• Built a reliable hours-per-point model over time</li>
              </ul>
            </div>

            {/* Result */}
            <div>
              <h2 className="text-2xl font-bold text-gray-900 mb-4">The Result</h2>
              <p className="text-gray-700 leading-relaxed text-lg">
                With no more blockers and a leaner workflow, the team moved faster and more confidently. The client began trusting the data, saw fewer wasted hours, and could make faster decisions. As a direct result, they tripled their budget — not because they were asked to, but because the process finally made sense.
              </p>
            </div>
          </div>

          {/* Visual */}
          <div className="lg:sticky lg:top-8">
            <div className="bg-white p-8 rounded-2xl shadow-sm">
              <img
                src="/case_study_points_system.png"
                alt="Points System Process Diagram"
                className="w-full h-auto rounded-lg"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default CaseStudyContent;