/*
 * ⚠️  MIXPANEL TRACKING WARNING ⚠️
 *
 * This file contains core Mixpanel initialization and wrapper functions.
 * BEFORE making ANY changes to Mixpanel tracking in this file:
 * 1. Read MIXPANEL_USAGE_DOCUMENTATION.md thoroughly
 * 2. Understand the current tracking limits and rules
 *
 * AFTER making changes:
 * 1. Update MIXPANEL_USAGE_DOCUMENTATION.md if needed
 * 2. Test in development mode (events logged to console)
 *
 * Current tracking: Core Mixpanel initialization
 */

import mixpanel from 'mixpanel-browser';

// Initialize Mixpanel
// Replace 'YOUR_MIXPANEL_TOKEN' with your actual Mixpanel project token
const MIXPANEL_TOKEN = process.env.REACT_APP_MIXPANEL_TOKEN || '8ff63702b08c01a15ccfcd1f73859d30';

mixpanel.init(MIXPANEL_TOKEN, {
  debug: process.env.NODE_ENV === 'development',
  track_pageview: true,
  persistence: 'localStorage',
});

// Wrapper functions for common Mixpanel operations
export const analytics = {
  // Track events
  track: (eventName, properties = {}) => {
    if (process.env.NODE_ENV === 'development') {
      console.log('Mixpanel Track:', eventName, properties);
    }
    mixpanel.track(eventName, properties);
  },

  // Identify users
  identify: (userId) => {
    if (process.env.NODE_ENV === 'development') {
      console.log('Mixpanel Identify:', userId);
    }
    mixpanel.identify(userId);
  },

  // Set user properties
  people: {
    set: (properties) => {
      if (process.env.NODE_ENV === 'development') {
        console.log('Mixpanel People Set:', properties);
      }
      mixpanel.people.set(properties);
    },
  },

  // Track page views
  trackPageView: (pageName, properties = {}) => {
    if (process.env.NODE_ENV === 'development') {
      console.log('Mixpanel Page View:', pageName, properties);
    }
    mixpanel.track('Page View', {
      page: pageName,
      ...properties,
    });
  },

  // Reset user data (useful for logout)
  reset: () => {
    if (process.env.NODE_ENV === 'development') {
      console.log('Mixpanel Reset');
    }
    mixpanel.reset();
  },
};

export default analytics;