import React, { useEffect } from 'react';
import { NavBar, HeroSection, CaseStudies, WorkTimeline, Skills, Achievements, Footer } from '../components';
import { ProblemsSection, SummarySection } from '../components/Hero';
import { analytics } from '../utils/mixpanel';

const Home = () => {
  useEffect(() => {
    // Track initial page load
    analytics.trackPageView('Portfolio Home', {
      timestamp: new Date().toISOString(),
      user_agent: navigator.userAgent,
      referrer: document.referrer || 'direct',
      screen_resolution: `${window.screen.width}x${window.screen.height}`,
      viewport_size: `${window.innerWidth}x${window.innerHeight}`,
    });

    // Track browser and device info
    analytics.track('Session Started', {
      timestamp: new Date().toISOString(),
      user_agent: navigator.userAgent,
      language: navigator.language,
      platform: navigator.platform,
      cookie_enabled: navigator.cookieEnabled,
      online: navigator.onLine,
    });
  }, []);

  return (
    <div className="min-h-screen bg-white">
      <NavBar />
      <HeroSection />
      <ProblemsSection />
      <SummarySection />
      <CaseStudies />
      <Skills />
      <WorkTimeline />
      <Achievements />
      <Footer />
    </div>
  );
};

export default Home;